<template>
  <div class="text-center">
    <v-pagination
      v-model="page"
      :length="dados.last_page"
      :current-page-aria-label="'page2'"
      :page-aria-label='"page=2"'
      prev-icon="mdi-menu-left"
      next-icon="mdi-menu-right"
      value="2"
      @input="novaPagina"
    ></v-pagination>
  </div>
</template>
<script>
export default {
    props:['dados'],
    data () {
        return {
            page: this.$store.state.paginaAtual
      }
    },
    methods: {
        novaPagina(){
            this.$store.state.paginaAtual = this.page
            this.$store.state.page = 'page='+this.page
            this.$emit('novaPaginaTeste', {dados: 'detalhe'})
        }
    },
}
</script>

