<template>
  <v-container class="mt-5 mb-5">
      <v-banner
        dark
        elevation="20"
        color="grey darken-3"
        >
            <h2>Projetos Realizados</h2>
        </v-banner>
        <lista-projetos :dados="projetos" class="mt-2"></lista-projetos>
      <paginacao :dados="projetos" @novaPaginaTeste="novaPagina"></paginacao>
    </v-container>
</template>

<script>
import ListaProjetos from '../components/projetos/ListaProjetos.vue'
import Paginacao from '../components/projetos/Paginacao.vue'
export default {
    components: {ListaProjetos, Paginacao},
    
    data() {
        return {
            urlProjeto:'http://angelofrancisco.site/api/projeto',
            projetos: [],
            quantidadeProjetos: '&quantidadeProjetos=3'
        }
    },
    methods: {
        carregarProjetos() {
            const axios = require('axios').default;
            axios.get(this.urlProjeto+ '?' + this.$store.state.page + this.quantidadeProjetos)
                .then(response => {
                    this.projetos = response.data
                })
                .catch(errors => {
                    console.log(errors)
                })
         },
         novaPagina() {
            this.carregarProjetos()
         }
    },
    mounted() {
        
        this.carregarProjetos()
    }
    
}
</script>

<style>


    
    .container{
        max-width: 1200px!important;
    }
</style>