<template>
  <v-container class="mt-5 mb-5">
      <sobre :dados="user" class="mb-5"></sobre>
      <v-divider dark></v-divider>
      <v-banner class="mt-5"
        dark
        elevation="20"
        color="grey darken-3"
        >
            <h2>Projetos Recentes</h2>
        </v-banner>
      <lista-projetos :dados="projetos" class="mt-1"></lista-projetos>
      <div class="w-100 d-flex justify-center mt-3">
        <v-btn dark elevation="20" @click="projetosUrl()" color="grey darken-3" light large right class="">
            Ver Projetos
        </v-btn>
      </div>
    </v-container>
</template>

<script>
import Sobre from '../components/inicio/Sobre.vue'
import ListaProjetos from '../components/projetos/ListaProjetos.vue'
export default {
    components: {Sobre, ListaProjetos},
    
    data() {
        return {
            urlUsuario: 'http://angelofrancisco.site/api/user',
            user: [],
            urlProjeto:'http://angelofrancisco.site/api/projeto',
            projetos: [],
            urlPaginacao: 'page=1',
            quantidadeProjetos: '&quantidadeProjetos=3',
        }
    },
    methods: {
        carregarAngelo() {
            const axios = require('axios').default;
            axios.get(this.urlUsuario+'/1')
                .then(response => {
                    this.user = response.data
                })
                .catch(errors => {
                    console.log(errors)
                })
        },
        carregarProjetos() {
            const axios = require('axios').default;
            axios.get(this.urlProjeto+ '?' + this.urlPaginacao + this.quantidadeProjetos)
                .then(response => {
                    this.projetos = response.data
                })
                .catch(errors => {
                    console.log(errors)
                })
        },
        projetosUrl(){
            if(this.$router.currentRoute.name === "Projetos"){
                return 0
            }
            this.$store.state.paginaAtual = 1
            this.$store.state.page = "page=1"
            return this.$router.push({ name: 'Projetos', params: { projetos: 'projetos' } })
        }
    },
    mounted() {
        this.carregarAngelo()
        this.carregarProjetos()
    }
    
}
</script>

<style>


    
    .container{
        max-width: 1200px!important;
    }
</style>